:root{
  font-family: var(--font-family);
}
html{
  scroll-snap-type: y proximity;
  max-height: 100vh;
  overflow-y: scroll;
}
.info{
  display: flex;
  flex-direction: column;
}

section{
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  align-items: center;
  padding: 8rem 0 3em 0;
  scroll-snap-align: start;
}

hr.solid{
  border-top: 3px solid var(--color-black);
  width: 100%;
  margin: 0 0 2rem 0;
}

.hero{
  display: flex;
  
  align-items: flex-start;
  justify-content: center;
}

.hero-text{

}

  .hero-text h1 {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 3vw;
  }

.hero-text-variable{
  display: flex;
  flex-direction: row;
  gap: 0.4em;
}



.layout{
  display:flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
}

.title{
  flex:1;
}
.details{
  flex:2;
  display:flex;
  flex-direction: column;
  gap:2em;
  text-align: justify;
  padding-top: 1em;
  font-family: var(--font-text);
}
.items {
  padding: 0;
  display: inline-block;
}

.item{
  list-style: none;
  margin: auto;
  min-height: 50px;
  border-left: 0.375em solid #000000;
  padding: 0 0 2em 2em;
  position: relative;
}

.item:last-child{
  border-left: 0.375em solid transparent;
}

.item::before{
  position: absolute;
  left: -1.0625em;
  top: -0.0625em;
  content: " ";
  border: 0.375em solid var(--color-black);
  border-radius: 500%;
  background: var(--color-white);
  height: 1em;
  width: 1em;
  transition: all 500ms ease-in-out;
}

.item:hover::before {
  background: var(--color-current);
  transition: all 1000ms ease-in-out;
  transition: background-color 1s ease-in-out;
}

.item{
  display:flex;
  flex-direction: row;
}



.item-text{
  flex:3;
  display: flex;
  text-align: left;
}

.item:hover{
  color: var(--color-current);
  transition: all 0.3s ease-in-out;
  cursor: pointer;
  transition: color 1s ease-out;
}

.item-text-collapsible{
  /* list-style: none; */
  margin: 1em;
}
.bottom{
  scroll-snap-align: top;
}

@media (max-width: 60rem) {
  .info{
    font-size: 14px;
  }
}


@media (max-width: 40rem) {
  .hero{
    flex-direction: column;
    /* align-items: center; */
  }

  .hero-text{
    /* text-align: center; */
  }

  .hero-text h1{
    font-size: 6vw;
  }

  .hero-text-variable{
    flex-direction: row;
    /* gap:0; */
  }

  .layout{
    flex-direction: column;
    gap: 1em;
  }

  .details{
    font-size: 14px;
  }

  .details h2{
    font-size: 20px;
  }
  .details h3{
    font-size: 17px;
  }

  .item{
    margin-left: 0.5em;
    justify-content: center;
    flex-wrap: wrap;
    align-items: flex-start;
    text-align: left;
  }
  .item-right{
    flex:0;
  }
  .item-ellipse{
    width: 1em;
    height:1em;
    margin-inline: 1em;
    border: 0.2em solid var(--color-black);
  }
  .item-text{
    width: 20em;
  }
  .item-logo{
    flex:none;
    overflow:hidden;
  }
}

@media (max-width: 22rem) {
  .info{
    font-size: 12px;
  }

  .details{
    font-size: 12px;
  }

  .details h2{
    font-size: 18px;
  }
  .details h3{
    font-size: 15px;
  }
  
}